<template>
  <div class="barrage" v-if="isDataLoaded">
    <el-breadcrumb class="path-nav" separator="/">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>弹幕管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table
      v-if="isImgDataLoaded"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="编号"
        type="index"
        width="180">
      </el-table-column>
      <el-table-column
        label="弹幕ID"
        prop="ID"
        width="180">
      </el-table-column>
      <el-table-column
        label="弹幕发送者ID"
        prop="userID"
        width="180">
      </el-table-column>
      <el-table-column
        label="图片ID"
        prop="imgID"
        width="180">
      </el-table-column>
      <el-table-column
        label="内容"
        prop="content"
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="操作">
        <template slot-scope="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="small"
            circle
            @click="unpass(scope.row.ID,scope.$index, tableData)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-alert
      v-show="!hasData"
      title="无数据"
      type="info">
    </el-alert>
    <div class="pageList" v-show="hasData">
      <div class="block">
        <el-pagination
          @current-change="freshTableByPage"
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="rawTableData.length">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'barrage',
  data () {
    return {
      dialogVisible: false,
      tableData: [],
      curIndex: 0,
      proxyInfo: '',
      isImgDataLoaded: false,
      pageSize: 5,
      rawTableData: [],
      pageTableData: [],
      nowShadowPic: '',
      isDataLoaded: false
    }
  },
  computed: {
    curInfo: {
      get: function () {
        return this.tableData[this.curIndex].name
      },
      set: function (val) {
        this.proxyInfo = val
      }
    },
    hasData () {
      if (this.pageTableData.length > 0) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    unpass (barrageID, index, rows) {
      var othis = this
      this.$http.post('/api/manageBarrage', {act: 'deleteBarrage', ID: barrageID}).then((res) => {
        let data = res.data

        if (parseInt(data.status) === 0) {
          othis.$message({
            message: '操作成功，弹幕已删除！',
            type: 'success'
          })
        } else {
          this.$message.error('操作失败，服务器出错！')
        }
      })
      rows.splice(index, 1)
    },
    freshTableByPage (page) {
      this.tableData = this.pageTableData[page - 1]
    },
    init () {
      let othis = this
      this.$http.post('/api/manageBarrage', {act: 'getBarrage'}).then((res) => {
        let data = res.data

        for (let i = 0; i < data.length; i++) {
          let once = {}
          once.ID = data[i].ID
          once.userID = data[i].userID
          once.imgID = data[i].imgID
          once.content = data[i].content
          othis.rawTableData.push(once)
        }
        // 对还未处理过的数据进行处理，得出分页数据
        let onceArr = []
        let num = 0
        for (let i = 0; i < othis.rawTableData.length; i++) {
          if (num === othis.pageSize) {
            num = 0
            othis.pageTableData.push(onceArr)
            onceArr = []
          }
          onceArr.push(othis.rawTableData[i])
          num++
        }
        if (onceArr.length > 0) {
          othis.pageTableData.push(onceArr)
        }
        if (othis.pageTableData.length > 0) {
          othis.tableData = othis.pageTableData[0]
        }
        if (othis.tableData.length > 0) {
          othis.isImgDataLoaded = true
        }
        othis.isDataLoaded = true
      })
    }
  },
  mounted () {
    this.init()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .barrage
    font-size 14px
    color #000
    .path-nav
      padding 15px
    .pageList
      margin-top 20px
      margin-left 40%
</style>



