<template>
  <div class="imagemanage" v-if="isDataLoaded">
    <el-breadcrumb class="path-nav" separator="/">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>图片管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table
      v-if="isImgDataLoaded"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="编号"
        type="index"
        width="180">
      </el-table-column>
      <el-table-column
        label="上传者"
        prop="uploader"
        width="180">
      </el-table-column>
      <el-table-column
        label="图片分类"
        prop="typename"
        width="180">
      </el-table-column>
      <el-table-column
        prop="image"
        label="图片"
        width="180">
        <template slot-scope="scope">
          <img :src="scope.row.src" style="height: 100px;width: 150px;cursor: zoom-in;" @click="scalePic(scope.row)">
        </template>
      </el-table-column>
      <el-table-column
        prop="address"
        label="操作">
        <template slot-scope="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="small"
            circle
            @click="unpass(scope.row.id,scope.$index, tableData)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-alert
      v-show="!hasData"
      title="无数据"
      type="info">
    </el-alert>
    <Shadow ref="shadow" :src="nowShadowPic"></Shadow>
    <div class="pageList" v-show="hasData">
      <div class="block">
        <el-pagination
          @current-change="freshTableByPage"
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="rawTableData.length">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import Shadow from '../Shadow/Shadow.vue'
// const PATH = 'http://localhost:8081/static/upload/'
const PATH = 'http://47.115.157.254:8081/static/upload/'

export default {
  name: 'imagemanage',
  data () {
    return {
      dialogVisible: false,
      tableData: [],
      curIndex: 0,
      proxyInfo: '',
      isImgDataLoaded: false,
      PATH: PATH,
      pageSize: 4,
      rawTableData: [],
      pageTableData: [],
      nowShadowPic: '',
      isDataLoaded: false
    }
  },
  computed: {
    curInfo: {
      get: function () {
        return this.tableData[this.curIndex].name
      },
      set: function (val) {
        this.proxyInfo = val
      }
    },
    hasData () {
      if (this.pageTableData.length > 0) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    scalePic (data) {
      this.$refs.shadow.ifshow = true
      this.nowShadowPic = data.src
    },
    unpass (imgId, index, rows) {
      var othis = this
      this.$http.post('/api/alterImgStatus', {act: 'unpass', imgId: imgId}).then((res) => {
        let data = res.data
        if (parseInt(data.status) === 0) {
          othis.$message({
            message: '操作成功，该图片下架！',
            type: 'success'
          })
          rows.splice(index, 1)
        } else {
          this.$message.error('操作失败，服务器出错！')
        }
      })
    },
    freshTableByPage (page) {
      this.tableData = this.pageTableData[page - 1]
    },
    init () {
      let othis = this
      this.$http.post('/api/getHistoryImg').then((res) => {
        let data = res.data

        for (let i = 0; i < data.length; i++) {
          let once = {}
          once.src = `${PATH}${data[i].src}`
          once.id = data[i].ID
          once.uploader = data[i].username
          once.imgType = data[i].TYPEID
          othis.rawTableData.push(once)
        }

        this.$http.post('/api/getTypes').then((res) => {
          let typeData = res.data

          if (typeData.length > 0) {
            for (let i = 0; i < othis.rawTableData.length; i++) {
              for (let j = 0; j < typeData.length; j++) {
                if (parseInt(othis.rawTableData[i].imgType) === parseInt(typeData[j].ID)) {
                  othis.rawTableData[i].typename = typeData[j].typename
                }
              }
            }
          }
          // 对还未处理过的数据进行处理，得出分页数据
          let onceArr = []
          let num = 0
          for (let i = 0; i < othis.rawTableData.length; i++) {
            if (num === othis.pageSize) {
              num = 0
              othis.pageTableData.push(onceArr)
              onceArr = []
            }
            onceArr.push(othis.rawTableData[i])
            num++
          }
          if (onceArr.length > 0) {
            othis.pageTableData.push(onceArr)
          }
          if (othis.pageTableData.length > 0) {
            othis.tableData = othis.pageTableData[0]
          }
          if (othis.tableData.length > 0) {
            othis.isImgDataLoaded = true
          }
          othis.isDataLoaded = true
        })
      })
    }
  },
  mounted () {
    this.init()
  },
  components: {
    Shadow
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .imagemanage
    font-size 14px
    color #000
    .path-nav
      padding 15px
    .pageList
      margin-top 20px
      margin-left 40%
</style>



