<template>
  <div class="manager">
    <div class="banner">
      <div class="logo">
        <img src="./title.png" alt="" style="width: 25px;height: 25px;margin-top: -5px;position: absolute;left: 17px;top: 11px;">
        <span>WeShare</span>
      </div>
      <div class="title">
        <div class="base-info">后台管理系统</div>
        <div class="person-info"></div>
      </div>
    </div>
    <div class="meanu">
      <div class="operate">
        <p class="control-icon" @click="triggerClick">
          <i :class="{'el-icon-d-arrow-right':isCollapse,'el-icon-d-arrow-left':!isCollapse}"></i>
        </p>
        <el-menu
          default-active="1-4-1"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          :collapse="isCollapse"
          background-color="#333744"
          text-color="#fff"
          active-background-color="#00C1DE">
          <el-menu-item index="1">
            <router-link to="/manager" tag="div">
              <i class="el-icon-s-data"></i>
              <span>首页</span>
            </router-link>
          </el-menu-item>
          <el-menu-item index="2">
            <router-link to="/manager/nav" tag="div">
              <i class="el-icon-picture-outline-round"></i>
              <span>图片审核</span>
            </router-link>
          </el-menu-item>
          <el-menu-item index="3">
            <router-link to="/manager/imagemanage" tag="div">
              <i class="el-icon-picture-outline"></i>
              <span>图片管理</span>
            </router-link>
          </el-menu-item>
          <el-menu-item index="4">
            <router-link to="/manager/barrage" tag="div">
              <i class="el-icon-s-operation"></i>
              <span>弹幕管理</span>
            </router-link>
          </el-menu-item>
          <el-menu-item index="5">
            <router-link to="/manager/usermanage" tag="div">
              <i class="el-icon-user-solid"></i>
              <span>用户管理</span>
            </router-link>
          </el-menu-item>
        </el-menu>
      </div>
      <div class="content">
        <router-view :mydata="routeData"/> <!--v-if="routeData!==null"-->
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'manager',
  data () {
    return {
      isCollapse: false,
      manageData: null
    }
  },
  methods: {
    handleOpen (key, keyPath) {
    },
    handleClose (key, keyPath) {
    },
    triggerClick () {
      this.isCollapse = !this.isCollapse
    },
    checkUser () {
      this.$http.post('/api/login', {'act': 'getLoginUser'}).then((res) => {
        let data = res.data
        let userId = parseInt(data.user_id)

        console.log(userId)
        if (userId === -1) {
          this.$router.replace({path: '/login'})
          return ''
        }
      })
    }
  },
  computed: {
    routeData () {
      if (this.manageData === null) {
        return null
      }
      return this.manageData.nav.barsDetail.Home
    }
  },
  mounted () {
    this.checkUser()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .manager
    width 100%
    height 100%
    background indianred
    color #fff
    font-size 20px
    .banner
      width 100%
      height 50px
      background blueviolet
      .logo
        min-width 180px
        height 100%
        background #373D41
        float left
        text-align center
        line-height 40px
      .title
        min-width calc(100% - 180px)
        height 100%
        background #373D41
        float left
        .base-info
          text-align center
          line-height 40px
    .meanu
      width 100%
      height calc(100% - 50px)
      display flex
      .operate
        max-width 180px
        height 100%
        background #333744
        float left
        .el-menu-vertical-demo:not(.el-menu--collapse)
          min-height: 400px
          width: 180px;
        .control-icon
          width 100%
          text-align center
          margin 10px 0 5px 0
          cursor pointer
          i
            font-size 20px
        .el-menu-vertical-demo
          overflow hidden
          border none
          box-sizing border-box
          max-width: 180px
      .content
        flex 1
        height 100%
        background #fff
        padding 5px
        float left
        overflow auto
</style>



