<template>
  <transition name="fade">
    <div class="shadow" v-show="ifshow">
      <PhoneView :status="status" :bottom="'-47px'" :src="`${src}`"></PhoneView>
      <div class="img">
        <div class="infoWrapper" v-show="status===0">
          <span class="close2" @click="ifshow=false"></span>
        </div>
      </div>
      <ul class="hidden-xs device">
        <li v-for="(val,index) in 5" :key="index" :class="{active:status===index}" @click="status=index"></li>
      </ul>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import PhoneView from '../PhoneView/PhoneView.vue'
const PATH = '/static/upload/'

export default {
  name: 'Shadow',
  props: {
    src: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      ifshow: false,
      status: 0,
      PATH: PATH
    }
  },
  methods: {

  },
  components: {
    PhoneView
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .fade-enter-active
    opacity 1
  .fade-enter-active,.fade-leave-active
    transition: 0.5s
  .fade-enter,.fade-leave-active
    opacity 0
  .shadow
    width 100%
    height 100%
    background rgba(0,0,0,0.7)
    position fixed
    left 0
    top 0
    z-index 10
    .img
      width 95%
      height calc(100% - 70px)
      position absolute
      left 2.5%
      top 50px
      background no-repeat center
      transition 0.5s
      background-size contain
      position relative
      &:hover
        .infoWrapper
          .collect,.like,p:nth-of-type(1),.contributor,.download
            opacity 1
      .infoWrapper
        width 100%
        height 100%
        p:nth-of-type(1)
          width 100%
          height 100px
          position absolute
          bottom 0
          background linear-gradient(to top,rgba(0,0,0,0.3),rgba(0,0,0,0))
          transition 0.5s
          opacity 0
          margin-bottom 0
        .contributor
          opacity 0
          color white
          width 30px
          height 30px
          line-height 30px
          text-indent 50px
          border-radius 15px
          background url("./images/contributor.jpg") no-repeat center
          background-size cover
          position absolute
          left 3%
          bottom 3%
          transition 0.5s
        .close2
          width 20px
          height 20px
          background url("./images/xx.png") no-repeat center
          background-size cover
          position absolute
          right 3.5%
          top 3.5%
          cursor pointer
          transition 0.5s
          opacity 1
          transform rotateZ(0deg)
          &:hover
            transform rotateZ(180deg)
    .device
      position absolute
      width 25px
      left 5px
      top calc(50% - 78px)
      li
        cursor pointer
        width 20px
        height 16px
        background url("./images/mon-spr.png") no-repeat 0px 0px
        margin-top 15px
        &:nth-of-type(1).active
          background-position 0 -15px
        &:nth-of-type(2)
          background url("./images/mon-spr.png") no-repeat -72px 0
          &.active
            background-position -72px -15px
        &:nth-of-type(3)
          background url("./images/mon-spr.png") no-repeat -92px 0
          &.active
            background-position -92px -15px
        &:nth-of-type(4)
          background url("./images/mon-spr.png") no-repeat -50px 0
          &.active
            background-position -50px -15px
        &:nth-of-type(5)
          background url("./images/mon-spr.png") no-repeat -25px 0
          &.active
            background-position -25px -15px

</style>



