<template>
  <div class="usermanage" v-if="isDataLoaded">
    <el-breadcrumb class="path-nav" separator="/">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table
      v-if="isImgDataLoaded"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="编号"
        type="index"
        width="130">
      </el-table-column>
      <el-table-column
        label="用户ID"
        prop="ID"
        width="130">
      </el-table-column>
      <el-table-column
        label="用户名"
        prop="username"
        width="130">
      </el-table-column>
      <el-table-column
        prop="avatar"
        label="头像"
        width="130">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" width="100" height="100">
        </template>
      </el-table-column>
      <el-table-column
        label="邮箱"
        prop="email"
        width="130">
      </el-table-column>
      <el-table-column
        label="状态"
        prop="uStatus"
        width="130">
      </el-table-column>
      <el-table-column
        prop="address"
        label="操作">
        <template slot-scope="scope">
        <el-button
          type="primary"
          icon="el-icon-check"
          size="small"
          circle
          title="解除封禁"
          @click="unprohibit(scope.row.ID,scope.$index, tableData)"></el-button>
          <el-button
            type="danger"
            icon="el-icon-circle-close"
            size="small"
            circle
            title="封禁"
            @click="prohibit(scope.row.ID,scope.$index, tableData)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-alert
      v-show="!hasData"
      title="无数据"
      type="info">
    </el-alert>
    <div class="pageList" v-show="hasData">
      <div class="block">
        <el-pagination
          @current-change="freshTableByPage"
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="rawTableData.length">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
// const PATH = 'http://localhost:8081/static/upload/'
const PATH = 'http://47.115.157.254:8081/static/upload/'

export default {
  name: 'usermanage',
  data () {
    return {
      dialogVisible: false,
      tableData: [],
      curIndex: 0,
      proxyInfo: '',
      isImgDataLoaded: false,
      PATH: PATH,
      pageSize: 4,
      rawTableData: [],
      pageTableData: [],
      nowShadowPic: '',
      isDataLoaded: false
    }
  },
  computed: {
    curInfo: {
      get: function () {
        return this.tableData[this.curIndex].name
      },
      set: function (val) {
        this.proxyInfo = val
      }
    },
    hasData () {
      if (this.pageTableData.length > 0) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    prohibit (userID, index, rows) {
      var othis = this
      this.$http.post('/api/manageUser', {act: 'prohibit', ID: userID}).then((res) => {
        let data = res.data

        if (parseInt(data.status) === 0) {
          othis.$message({
            message: '操作成功，用户已被封禁！',
            type: 'success'
          })
          rows[index].uStatus = '封禁'
        } else {
          this.$message.error('操作失败，服务器出错！')
        }
      })
    },
    unprohibit (userID, index, rows) {
      var othis = this
      this.$http.post('/api/manageUser', {act: 'unprohibit', ID: userID}).then((res) => {
        let data = res.data

        if (parseInt(data.status) === 0) {
          othis.$message({
            message: '操作成功，用户已被解除封禁！',
            type: 'success'
          })
          rows[index].uStatus = '正常'
          console.log(rows)
        } else {
          this.$message.error('操作失败，服务器出错！')
        }
      })
    },
    freshTableByPage (page) {
      this.tableData = this.pageTableData[page - 1]
    },
    init () {
      let othis = this
      this.$http.post('/api/manageUser', {act: 'getUser'}).then((res) => {
        let data = res.data
        for (let i = 0; i < data.length; i++) {
          let once = {}
          once.avatar = `${PATH}${data[i].avatar}`
          once.ID = data[i].ID
          once.username = data[i].username
          once.email = data[i].email
          once.uStatus = data[i].uStatus

          if (parseInt(once.uStatus) === 0) {
            once.uStatus = '正常'
          } else if (parseInt(once.uStatus) === 1) {
            once.uStatus = '封禁'
          }
          othis.rawTableData.push(once)
        }
        // 对还未处理过的数据进行处理，得出分页数据
        let onceArr = []
        let num = 0
        for (let i = 0; i < othis.rawTableData.length; i++) {
          if (num === othis.pageSize) {
            num = 0
            othis.pageTableData.push(onceArr)
            onceArr = []
          }
          onceArr.push(othis.rawTableData[i])
          num++
        }
        if (onceArr.length > 0) {
          othis.pageTableData.push(onceArr)
        }
        if (othis.pageTableData.length > 0) {
          othis.tableData = othis.pageTableData[0]
        }
        if (othis.tableData.length > 0) {
          othis.isImgDataLoaded = true
        }
        othis.isDataLoaded = true
      })
    }
  },
  mounted () {
    this.init()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .usermanage
    font-size 14px
    color #000
    .path-nav
      padding 15px
    .pageList
      margin-top 20px
      margin-left 40%
</style>



